@import "../../App.scss";

.gameTimers {
  text-align: center;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.timer {
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin: 10px;

  > h1 {
    display: flex;
  }

  > .buttons {
    display: flex;

    button {
      margin: 10px;
    }
  }
}
